import _ from 'lodash'
import moment from 'moment'

import Auth from '../../mixins/auth'

import PlayerSearch from '../player-search'

import Utils from '../../utils'

export default {
  name: 'BetBox',

  mixins: [
    Auth
  ],

  components: {
    PlayerSearch
  },

  data() {
    return {
      draws: [],
      player: {
        agent_id: null,
        player_id: null,
        contact_no: null,
        combinations: []
      },
      playerRules: {
        player_id: [
          {
            required: true,
            message: 'The player field is required.',
            trigger: 'blur'
          },
          {
            required: true,
            message: 'The player field is required.',
            trigger: 'change'
          }
        ],
        contact_no: [
          {
            required: true,
            message: 'Please input contact # (ex. 09101234567)',
            trigger: 'blur'
          },
          {
            required: true,
            message: 'Please input contact # (ex. 09101234567)',
            trigger: 'change'
          },
          // {
          //   validator(rule, value, callback) {
          //     if (value.length < 11) {
          //       callback(new Error('Invalid contact #'))
          //     } else {
          //       callback()
          //     }
          //   },
          //   trigger: 'change'
          // }
        ]
      },
      entry: {
        draw_id: null,
        bet: 0,
        isramble: 'S',
        input: ''
      },
      entryRules: {
        input: [
          {
            required: true,
            message: 'Please input combination',
            trigger: 'blur'
          }
        ]
      },
      showDialog: false,
      inProgress: false,
      isLoading: false
    }
  },

  computed: {
    draw() {
      return this.entry.draw_id && this.draws.length ?
        this.draws.find(d => d.id == this.entry.draw_id) : null
    },

    drawsMap() {
      return this.draws.reduce((map, d) => {
        map[d.id] = d
        return map
      }, {})
    },

    maxInput() {
      if (this.draw) {
        let {
          numberformat,
          maxinput
        } = this.draw
        let digitLen = numberformat.split('-')[1].length

        return maxinput * digitLen
      }

      return null
    },

    dialog() {
      return {
        date: moment().format('MMMM DD, YYYY')
      }
    },

    validSend() {
      return this.player.player_id != null
        && this.player.contact_no
        && this.player.combinations.length
    },

    validClear() {
      return this.player.combinations.length > 0
    },

    validQueue() {
      let entry = this.entry

      return entry.input.length == this.maxInput
    }
  },

  async mounted() {
    this.reset()
  },

  methods: {
    handleClose(done) {
      if (!this.inProgress) {
        this.reset()

        if (typeof done == 'function') {
          done()
        }
      }
    },

    setContact(contact) {
      this.player.contact_no = contact
    },

    reset() {
      this.player = {
        agent_id: this.$user.id,
        player_id: null,
        contact_no: null,
        combinations: []
      }

      this.entry = {
        bet: null,
        isramble: 'S',
        input: ''
      }
    },

    openDialog() {
      this.showDialog = true
    },

    closeDialog() {
      this.showDialog = false
      this.reset()
    },

    isNumber(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },

    removeEntry(entry) {
      let index = this.player.combinations.indexOf(entry)

      if (index > -1) {
        this.player.combinations.splice(index, 1)
      }
    },

    clearEntries() {
      this.player.combinations = []
    },

    onDrawChange() {
      if (this.entry.draw_id) {
        this.entry.bet = this.draw.minimumbet
        this.entry.isramble = 'S'
        this.entry.input = ''
      } else {
        this.entry = {
          bet: null,
          isramble: 'S',
          input: ''
        }
      }
    },

    async fetch() {
      if (this.isLoading) return

      try {
        this.isLoading = true
        let { data } = await this.$store.dispatch('bet/list')
        this.draws = data

        if (data.length) {
          this.openDialog()
        } else {
          this.$message({
            type:'warning',
            message: 'No active draws.'
          })
        }
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },

    async handleEntry() {
      try {
        let draw = this.draw
        let entry = this.entry
        let digitLen = draw.numberformat.split('-')[1].length
        let combination = Utils.Format.combination(this.entry.input, digitLen)
        let identifier = entry.input
        let draw_id = draw.id
        let draw_name = draw.name
        let max_accumulated = draw.max_accumulated

        if (draw.draw_type == '3d_rambol') {
          if (entry.isramble == 'R') {
            let combArr = Utils.Math.permutation(combination)
            let permuted = combArr.map(d => {
              return {
                identifier,
                combination: d.join('-'),
                isramble: entry.isramble,
                bet: entry.bet
              }
            })

            this.player.combinations.push({
              draw_name,
              draw_id,
              identifier,
              permuted,
              max_accumulated,
              combination: combination.join('-'),
              isramble: entry.isramble,
              bet: entry.bet
            })
          } else {
            this.player.combinations.push({
              draw_name,
              draw_id,
              identifier,
              permuted: [],
              max_accumulated,
              combination: combination.join('-'),
              bet: entry.bet,
              isramble: entry.isramble
            })
          }
        } else {
          this.player.combinations.push({
            draw_name,
            draw_id,
            identifier,
            permuted: [],
            max_accumulated,
            combination: combination.join('-'),
            bet: entry.bet,
            isramble: entry.isramble
          })
        }
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }
    },

    async queue() {
      if (!this.validQueue) return

      try {
        await this.handleEntry()

        this.entry.input = ''
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }
    },

    async checkValidity() {
      let server = await this.$store.dispatch('server')
      let entries = this.player.combinations

      entries.forEach(c => {
        let draw = this.drawsMap[c.draw_id]
        let sTime = moment(server.server).format('HH:mm')
        let cTime = moment(draw.timetoclose, 'HH:mm:ss').format('HH:mm')

        c.draw = draw.name
        c.valid = sTime < cTime
      })

      return {
        isValid: entries.filter(c => !c.valid).length == 0,
        invalid: [...new Set(entries.filter(c => !c.valid).map(c => c.draw))]
      }
    },

    async handleErroMessage(message) {
      return new Promise((resolve) => {
        setTimeout(() => {
          this.$notify.error({
            title: 'Error',
            duration: 0,
            message
          })
          resolve()
        }, 250)
      })
    },

    async send() {
      if (this.inProgress) return
      this.inProgress = true

      let info = await this.checkValidity()

      if (info.isValid) {
        try {

          await this.$store.dispatch('bet/send', this.player)

          this.closeDialog()
          this.$emit('done')
          this.$message({
            type: 'success',
            message: 'Bet successful!'
          })
        } catch(e) {
          let { response } = e

          if (response) {
            let { data } = response

            if (   data
                && data.message) {
              if (_.isArray(data.message)) {
                let index = 0
                while (data.message[i]) {
                  let msg = data.message[i]
                  await this.handleErroMessage(msg)
                  index++
                }
              }
            } else {
              this.$message({
                type: 'error',
                message: e.message
              })
            }
          } else {
            this.$message({
              type: 'error',
              message: e.message
            })
          }
        }
      } else {
        let index = 0
        while (info.invalid[index]) {
          let msg = info.invalid[index]
          await this.handleErroMessage(msg)
          index++
        }
      }

      this.inProgress = false
    }
  }
}



