import Paginate from '../../mixins/paginate'

import BetBox from '../../components/bet-box'
import DrawDropdown from '../../components/draw-dropdown'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Paginate
  ],

  components: {
    BetBox,
    DrawDropdown
  },

  data() {
    return {
      intervalTimeRef: null,
      intervalTime: null,
      action: null,
      list: {
        data: [],
        recordsFiltered: 0,
        totalSales: 0
      },
      filter: {
        draw: [],
        daterange: null,
        status: null
      },
      isLoading: true,
      route,
    }
  },

  computed: {
    currentTime() {
      return this.intervalTime
    },

    tableHeight() {
      return window.innerHeight - 250
    }
  },

  destroyed() {
    clearInterval(this.intervalTimeRef)
  },

  async mounted() {
    await this.fetch()

    this.intervalTime = await this.getTime()
    this.intervalTimeRef = setInterval(async () => {
      this.intervalTime = await this.getTime()
    }, 60000)
  },

  methods: {
    async getTime() {
      let server = await this.$store.dispatch('server')

      return new Date(server.server).getTime()
    },

    async fetch(reset) {
      this.isLoading = true

      try {
        if (reset) {
          this.currPage = 1
        }

        let params = {
          start: (this.currPage - 1) * this.currPageSize,
          length: this.currPageSize,

          // Global search
          'search[value]': this.search,

          // Agent
          'columns[0][data]': 'full_name',
          'columns[0][name]': 'full_name',
          'columns[0][searchable]': true,
          'columns[0][orderable]': true,
          'columns[0][search][value]': '',
          'columns[0][search][regex]': true,

          // Player
          'columns[1][data]': 'username',
          'columns[1][name]': 'username',
          'columns[1][searchable]': true,
          'columns[1][orderable]': true,
          'columns[1][search][value]': '',
          'columns[1][search][regex]': true,

          // Contact #
          'columns[2][data]': 'contact_no',
          'columns[2][name]': 'contact_no',
          'columns[2][searchable]': true,
          'columns[2][orderable]': true,
          'columns[2][search][value]': '',
          'columns[2][search][regex]': true,

          // Reference #
          'columns[3][data]': 'referenceno',
          'columns[3][name]': 'referenceno',
          'columns[3][searchable]': true,
          'columns[3][orderable]': true,
          'columns[3][search][value]': '',
          'columns[3][search][regex]': true,

          // Combination
          'columns[4][data]': 'combination',
          'columns[4][name]': 'combination',
          'columns[4][searchable]': true,
          'columns[4][orderable]': true,
          'columns[4][search][value]': '',
          'columns[4][search][regex]': true,

          // Bet amount
          'columns[5][data]': 'bet',
          'columns[5][name]': 'bet',
          'columns[5][searchable]': true,
          'columns[5][orderable]': true,
          'columns[5][search][value]': '',
          'columns[5][search][regex]': true,
        }

        if (this.filter.daterange) {
          let dateFormat = 'MM-DD-YYYY'
          params.from = moment(this.filter.daterange[0]).format(dateFormat)
          params.to = moment(this.filter.daterange[1]).format(dateFormat)
        }

        if (this.filter.draw.length) {
          params.draw = this.filter.draw.join(',')
        }

        if (this.filter.status) {
          params.status = this.filter.status
        }

        let list = await this.$store.dispatch('bet_history/list', params)

        if (list.totalSales) {
          this.list.totalSales = list.totalSales
        }

        this.list.totalSales = list.totalSales
        this.list.data = list.data.map(d => {
          d.time = new Date(d.drawdate).getTime()
          return d
        })
        this.list.recordsFiltered = list.recordsFiltered
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },

    async voidBet(row) {
      if (this.isLoading) return

      this.isLoading = true

      try {
        await this.$store.dispatch('bet_history/void', row.id)
        await this.fetch()
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    }
  }
}



